//React component that stores the tutor accout processing for users
import React from 'react';
import "./AnswerPage.css";
import {FaHome} from 'react-icons/fa';
//getting the question information from the table that was stored in the localstorage
const topic = localStorage.getItem("topic");
const description = localStorage.getItem("description");
const studentName = localStorage.getItem("studentName");
const professor = localStorage.getItem("professor");
const course = localStorage.getItem("courseName");
const timeStamp = localStorage.getItem('timeStamp');
const status = localStorage.getItem('status');
// console.log(questionInfo);
class AnswerPage extends React.Component{
    render(){
        return(
            <div className="show-data">
                <div className="navBar_01">
                    <FaHome />
                </div>
                <div className="question-info">
                    <div className="Topic">
                        <h3>{topic}</h3>

                        <div className="Course">
                            Course: <span>{course}</span>
                        </div>
                    </div>
                    <div className="Description">
                            <span>Description:</span>
                            <br></br> 
                            <p>{description}</p>
                    </div>
                    <div className="case-info">
                        <div className="info1">
                            <div className="student-name">
                                <span>Student Username: </span>
                                <p>{studentName}</p>
                            </div>
                            <div className="prof-name">
                                    <span>Professor Name: </span>
                                    <p>{professor}</p>
                            </div>
                        </div>
                        <div className="info2">
                            <div className="time-stamp">
                                <span>Recieved at: </span>
                                <p>{timeStamp}</p>
                            </div>
                            <div className="Status">
                                <span>Status: </span>
                                <p>{status}</p>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="answer-question">
                    <form onSubmit={(e) => sendAnswer(e)}>
                        <div className="answer-box">
                            <textarea className="answerBox" id="explanation" placeholder="Enter your resolution of the issue..."></textarea>
                        </div>
                        <div className="sendAns">
                                <button type="submit" className="btn">Submit Response</button>
                        </div>
                    </form>
                </div>
            </div>
        )
    }
}

//Function to send the answer to the user 
function sendAnswer(e){
    e.preventDefault();
    const answer = document.getElementById('explanation').value
    console.log(answer);
}

export default AnswerPage;
